import React, { useState, PropsWithChildren, ReactElement } from 'react';
import { useSelector, useDispatch } from 'react-redux';
import { useHistory } from 'react-router-dom';
import { useSnackbar } from 'notistack';
import AppBar from '@material-ui/core/AppBar';
import Toolbar from '@material-ui/core/Toolbar';
import Typography from '@material-ui/core/Typography';
import Button from '@material-ui/core/Button';
import IconButton from '@material-ui/core/IconButton';
import Select from '@material-ui/core/Select';
import Menu from '@material-ui/core/Menu';
import MenuItem from '@material-ui/core/MenuItem';
import LockIcon from '@material-ui/icons/Lock';
import PersonAddIcon from '@material-ui/icons/PersonAdd';
import ExploreIcon from '@material-ui/icons/Explore';
import ExitToAppIcon from '@material-ui/icons/ExitToApp';
import PoolIcon from '@material-ui/icons/Pool';
import BubbleChartIcon from '@material-ui/icons/BubbleChart';
import * as registry from '@project-serum/registry';
import { networks } from '@project-serum/common';
import { WalletConnectButton, useWallet } from './Wallet';
import {
  State as StoreState,
  ProgramAccount,
  BootstrapState,
} from '../../store/reducer';
import { ActionType } from '../../store/actions';
import { ViewTransactionOnExplorerButton } from './Notification';
import Brightness1Icon from '@material-ui/icons/Brightness1';

import { makeStyles } from '@material-ui/core/styles';
import LinearProgress from '@material-ui/core/LinearProgress';

const useStyles = makeStyles(theme => ({
  root: {
    width: '100%',
    '& > * + *': {
      marginTop: theme.spacing(2),
    },
  },
}));

type Props = {};

export default function Layout(props: PropsWithChildren<Props>) {
  return <Nav>{props.children}</Nav>;
}

function Nav(props: PropsWithChildren<Props>): ReactElement {
  const { isAppReady } = useSelector((state: StoreState) => {
    return {
      isAppReady:
        state.common.isWalletConnected &&
        state.common.bootstrapState === BootstrapState.Bootstrapped,
    };
  });
  return (
    <div
      style={{
        display: 'flex',
        minHeight: '100vh',
        flexDirection: 'column',
        backgroundColor: 'rgb(251, 251, 251)',
      }}
    >
      <NavBar isAppReady={isAppReady} />
      <div
        style={{
          flex: 1,
          display: 'flex',
          marginBottom: '30px', // Compensates for the fixed position footer.
        }}
      >
        {!isAppReady ? (
          <Disconnected />
        ) : (
          <div style={{ width: '100%' }}>{props.children}</div>
        )}
      </div>
      <RiskBar />
    </div>
  );
}

function RiskBar() {
  const { isAppReady, isDisconnected, network, slotInfo } = useSelector(
    (state: StoreState) => {
      return {
        network: state.common.network,
        slotInfo: state.solana.slotInfo,
        isAppReady:
          state.common.isWalletConnected &&
          state.common.bootstrapState === BootstrapState.Bootstrapped,
        isDisconnected: !state.common.isWalletConnected,
      };
    },
  );
  return (
    <div
      style={{
        position: 'fixed',
        width: '100%',
        bottom: 0,
        textAlign: 'center',
        height: '30px',
        backgroundColor: 'rgb(245, 0, 87)',
        color: '#fef7f9',
        display: 'flex',
        justifyContent: 'space-between',
        paddingLeft: '10px',
        paddingRight: '10px',
      }}
    >
      <div
        style={{
          display: 'flex',
        }}
      >
        <div
          style={{
            display: 'flex',
            justifyContent: 'center',
            flexDirection: 'column',
            marginRight: '10px',
          }}
        >
          <Brightness1Icon
            style={{
              color: isAppReady ? 'green' : isDisconnected ? 'white' : 'blue',
              fontSize: '12px',
            }}
          />
        </div>
        <div
          style={{
            display: 'flex',
            justifyContent: 'center',
            flexDirection: 'column',
          }}
        >
          <Typography
            style={{ color: 'white', fontSize: '14px', fontWeight: 'bold' }}
          >
            {network.label}
          </Typography>
        </div>
      </div>
      {isAppReady && (
        <div
          style={{
            display: 'flex',
            justifyContent: 'center',
            flexDirection: 'column',
          }}
        >
          <Typography style={{ fontSize: '14px', fontWeight: 'bold' }}>
            Slot {slotInfo?.slot.toString()}
          </Typography>
        </div>
      )}
    </div>
  );
}

type NavBarProps = {
  isAppReady: boolean;
  member?: ProgramAccount<registry.accounts.Member>;
};

function NavBar(props: NavBarProps) {
  const { isAppReady } = props;
  return (
    <AppBar
      position="static"
      style={{
        background: '#172026',
        color: 'white',
      }}
    >
      <Toolbar>
        <div
          style={{
            display: 'flex',
            justifyContent: 'space-between',
            width: '100%',
          }}
        >
          <div style={{ display: 'flex' }}>
            <SerumLogoButton />
            <BarButton label="Stake" hrefClient="/" />
            <BarButton label="Trade" href="https://dex.projectserum.com" />
            <BarButton label="Swap" href="https://swap.projectserum.com" />
            <BarButton
              label="Learn"
              href="https://serum-academy.com/en/serum-dex/"
            />
          </div>
          <div
            style={{
              display: 'flex',
            }}
          >
            {!isAppReady && <NetworkSelector />}
            <WalletConnectButton
              style={{
                display: isAppReady ? 'none' : '',
              }}
            />
            {isAppReady && <UserWalletDropdown />}
          </div>
        </div>
      </Toolbar>
    </AppBar>
  );
}

function Disconnected() {
  const classes = useStyles();
  const { network, isDisconnected } = useSelector((state: StoreState) => {
    return {
      network: state.common.network,
      isDisconnected: !state.common.isWalletConnected,
    };
  });
  return (
    <div
      style={{
        flex: '1',
        display: 'flex',
        justifyContent: 'center',
        flexDirection: 'column',
        textAlign: 'center',
      }}
    >
      <div style={{ width: '100%', display: 'flex' }}>
        {isDisconnected ? (
          <div
            style={{ display: 'flex', marginLeft: 'auto', marginRight: 'auto' }}
          >
            <Typography
              style={{ marginLeft: '24px' }}
              color="textSecondary"
              variant="h4"
            >
              Disconnected
            </Typography>
          </div>
        ) : (
          <div style={{ width: '100%' }}>
            <div style={{ width: '100%' }} className={classes.root}>
              <div>
                <Typography variant="h5">
                  {`Connecting to ${network.label}...`}
                </Typography>
              </div>
              <div
                style={{
                  marginLeft: 'auto',
                  marginRight: 'auto',
                  width: '300px',
                }}
              >
                <LinearProgress style={{ width: '100%' }} />
              </div>
            </div>
          </div>
        )}
      </div>
    </div>
  );
}

function SerumLogoButton() {
  const history = useHistory();
  return (
    <div style={{ display: 'flex' }} onClick={() => history.push('/')}>
      <Button color="inherit">
        <div
          style={{
            display: 'flex',
            flexDirection: 'column',
            justifyContent: 'center',
          }}
        >
          <img
            style={{
              display: 'block',
              height: '35px',
            }}
            alt="Logo"
            src="http://dex.projectserum.com/static/media/logo.49174c73.svg"
          />
        </div>
      </Button>
    </div>
  );
}

type BarButtonProps = {
  label: string;
  hrefClient?: string;
  href?: string;
};

function BarButton(props: BarButtonProps) {
  const history = useHistory();
  const { label, href, hrefClient } = props;
  return (
    <div
      style={{ display: 'flex' }}
      onClick={() => hrefClient && history.push(hrefClient)}
    >
      <Button color="inherit" href={href}>
        <div
          style={{
            display: 'flex',
            justifyContent: 'center',
            flexDirection: 'column',
          }}
        >
          <Typography style={{ fontSize: '15px' }}>{label}</Typography>
        </div>
      </Button>
    </div>
  );
}

function UserWalletDropdown() {
  const history = useHistory();

  return (
    <>
      <div onClick={() => history.push('/registry/entities')}>
        <IconButton color="inherit">
          <ExploreIcon />
        </IconButton>
      </div>
      <div onClick={() => history.push('/registry/pool')}>
        <IconButton color="inherit">
          <PoolIcon />
        </IconButton>
      </div>
      <div onClick={() => history.push('/lockup')}>
        <IconButton color="inherit">
          <LockIcon />
        </IconButton>
      </div>
      <UserSelector />
    </>
  );
}

function NetworkSelector() {
  const network = useSelector((state: StoreState) => {
    return state.common.network;
  });
  const dispatch = useDispatch();
  const [anchorEl, setAnchorEl] = useState(null);

  const handleClose = () => {
    setAnchorEl(null);
  };

  return (
    <div
      style={{
        marginRight: '10px',
        fontSize: '15px',
        display: 'flex',
        justifyContent: 'center',
        flexDirection: 'column',
      }}
    >
      <Button
        color="inherit"
        onClick={e =>
          setAnchorEl(
            // @ts-ignore
            e.currentTarget,
          )
        }
      >
        <BubbleChartIcon />
        <Typography style={{ marginLeft: '5px', fontSize: '15px' }}>
          {network.label}
        </Typography>
      </Button>
      <Menu
        anchorEl={anchorEl}
        open={Boolean(anchorEl)}
        onClose={handleClose}
        style={{
          marginLeft: '12px',
          color: 'white',
        }}
      >
        {Object.keys(networks).map((n: string) => (
          <MenuItem
            onClick={() => {
              handleClose();
              dispatch({
                type: ActionType.CommonSetNetwork,
                item: {
                  network: networks[n],
                },
              });
            }}
          >
            <Typography>{networks[n].label}</Typography>
          </MenuItem>
        ))}
      </Menu>
    </div>
  );
}

function UserSelector() {
  const { wallet } = useWallet();
  const dispatch = useDispatch();
  const { enqueueSnackbar, closeSnackbar } = useSnackbar();
  const { registryClient } = useWallet();
  const { network, member } = useSelector((state: StoreState) => {
    return {
      member: state.registry.member,
      network: state.common.network,
    };
  });

  const createStakeAccount = async () => {
    enqueueSnackbar('Creating stake account', {
      variant: 'info',
    });

    // TODO: separate member creation from entity joining (i.e., make it so that
    //       entity doesn't need to be specified here).
    const entity = network.defaultEntity;
    const { tx, member } = await registryClient.createMember({
      entity,
    });
    const memberAcc = await registryClient.accounts.member(member);
    dispatch({
      type: ActionType.RegistrySetMember,
      item: {
        member: {
          publicKey: member,
          account: memberAcc,
        },
      },
    });
    closeSnackbar();
    enqueueSnackbar(`Stake account created ${member}`, {
      variant: 'success',
      action: <ViewTransactionOnExplorerButton signature={tx} />,
    });
  };
  return (
    <Select
      displayEmpty
      renderValue={() => {
        return (
          <Typography style={{ overflow: 'hidden' }}>
            {wallet.publicKey.toString()}
          </Typography>
        );
      }}
      style={{
        marginLeft: '12px',
        width: '150px',
        color: 'white',
      }}
      onChange={e => {
        if (e.target.value === 'disconnect') {
          wallet.disconnect();
        }
      }}
    >
      {member === undefined && (
        <MenuItem value="create-member">
          <div onClick={() => createStakeAccount()}>
            <IconButton color="inherit">
              <PersonAddIcon />
              <Typography style={{ marginLeft: '15px' }}>
                Create stake account
              </Typography>
            </IconButton>
          </div>
        </MenuItem>
      )}
      <MenuItem value="disconnect">
        <IconButton color="inherit">
          <ExitToAppIcon />
          <Typography style={{ marginLeft: '15px' }}>Disconnect</Typography>
        </IconButton>
      </MenuItem>
    </Select>
  );
}
